#front-page-overlay-wrapper
{
    position: fixed;
    z-index: 9999;
    top:0rem;
    bottom:0rem;
    left:0rem;
    right:0rem;
    background: #ffffff80;
}

#front-page-overlay-container
{
    position: relative;
    margin: 0 2rem;
    top:50%;
    transform: translateY(-50%);
    
}

.overlay-notice>*
{
    padding:0;
    margin:0;
}
.overlay-notice
{
    position: relative;
    min-height: 50vh;
    min-width: 50vw;
    padding: 2rem;
    background: #000000fa;
    background-image: url('/assets/img/buried.png');
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color:white;
    box-shadow: 0px 0px 1rem #888;
    border: .5rem solid #fff;
}

.overlay-close-btn
{
    background: red;
    clip-path: circle(24px);
    border: none;
    width: 50px;
    height: 50px;
    margin: .5rem;
    position: absolute;
    top: 0;
    right: 0;
    img
    {
        color: red;
        width: 100%;
    }
}